import {
    Button,
    Container, 
    Card, 
    Image
} from 'semantic-ui-react'; 


const parseSubjects = (props) => {
    let subjects = ""
    props.blog.category.forEach(item => {return subjects += <Item></Item> })
    return subjects
}

const BlogCard = (props) => {
    return(
        <Card>
            <Card.Header>
                {props.blog.pubDate[0]}
            </Card.Header>
            <Card.Description>
                <h2>
                    {props.blog.title[0]}
                </h2>
            </Card.Description>
            <Card.Content>
             Subjects:
             <ul>
             {
              parseSubjects(props)
             }
             </ul>
            </Card.Content>
            
        </Card>
    )
}

export default BlogCard


